.welcome-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  background: linear-gradient(to bottom right, #55CDFC, #F7A8B8);
  font-family: 'Arial', sans-serif;
}

.avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 20px;
}

h1 {
  color: #333;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

p {
  color: #666;
  font-size: 1.2rem;
  margin: 10px 0;
}

.links {
  margin-top: 20px;
}

.link-button {
  text-decoration: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  margin: 0 10px;
  transition: background-color 0.3s ease;
}

.twitter {
  background-color: #1DA1F2;
  color: white;
}

.twitter:hover {
  background-color: #0d8ddb;
}

.instagram {
  background-color: #E4405F;
  color: white;
}

.instagram:hover {
  background-color: #c32f4a;
}

.email {
  background-color: #34a853;
  color: white;
}

.email:hover {
  background-color: #2c8e43;
}

.telegram {
  background-color: #0088cc;
  color: white;
}

.telegram:hover {
  background-color: #006c99;
}
